<template>
    <div class="capTable loan" ref="CapTable">
        <div class="title">CapTable History</div>
        <div class="loan-table-container">
            <h3 class="h3">CapTable History</h3>
            <div class="table-content">
            <el-table :data="item.baseName" style="width: 100%" v-for="(item,index) in firstContentList" :key="index+1">
                <el-table-column label="" width="150">
                    <el-table-column prop="sharetype" label="" width="130"></el-table-column>
                    <el-table-column prop="securitytypeidstr" label="ShareType" width="130"></el-table-column>
                </el-table-column>
                <el-table-column :label="item.baseId">
                    <el-table-column prop="cost" label="" width="130" :formatter="numberFormat"></el-table-column>
                    <el-table-column prop="shareownedno" label="" width="130" :formatter="numberFormat"></el-table-column>
                    <el-table-column prop="proper" label="(%)" width="130" :formatter="properFormat"></el-table-column>
                    <el-table-column prop="properwithoutesop" label="withoutESOP(%)" width="130" :formatter="properFormat"></el-table-column>
                </el-table-column>
            </el-table>
            <el-table :data="item.baseName" style="width: 100%" v-for="(item,index) in otherContentList" :key="index">
                <el-table-column :label="item.baseId">
                    <el-table-column prop="cost" label="" width="130" :formatter="numberFormat"></el-table-column>
                    <el-table-column prop="shareownedno" label="" width="130" :formatter="numberFormat"></el-table-column>
                    <el-table-column prop="proper" label="(%)" width="130" :formatter="properFormat"></el-table-column>
                    <el-table-column prop="properwithoutesop" label="withoutESOP(%)" width="130" :formatter="properFormat"></el-table-column>
                </el-table-column>
            </el-table>
            </div>
        </div>
    </div>
</template>
<script>
import axioss from '@/api/axios';
import * as method from "@/api/method";
import bus from "@/api/eventbus";
export default {
    name:"CapTable",
    data(){
        return {
            heightObj:'',
            capVisible:false,
            capSelectList:[],
            capTableHeadList:[],
            capTableContentList:[],
            firstContentList:[],
            otherContentList:[]
        }
    },
    updated(){
    },
    mounted(){
        bus.$on('toScorll',(ace,arr)=>{
           this.scrolltoview(ace,arr);
       });
       this.reqTableHead();
       this.reqTableContent();
    },
    methods:{
        reqdroplist(){
            var obj={dictArray:"DDL_ShareType"}
            axioss.reqdroplist(obj).then(res=>{
                this.capSelectList=res.data.data[0].baseInfoList;
            })
        },
      numberFormat: function (row, column) {
        var num = row[column.property];
        if (num == undefined) {
          return "";
        }
        return method.toThousands(num);
      },
      properFormat: function (row, column) {
        var num = row[column.property];
        if (num == undefined) {
          return "";
        }
        return (num*100).toFixed(2);
      },

        handleAdd(){
            this.reqdroplist();//
            this.capVisible=true;
        },
        reqTableHead(){
            var portfolioid=this.portfolioid;
            axioss.reqTableHead(portfolioid).then(res=>{
                console.log(res);
                this.capTableHeadList=res.data.data;
                this.capTableHeadList=this.toHeader(res.data.data);
                console.log(this.capTableHeadList);
            })
        },
        reqTableContent(){
            axioss.reqTableContent(this.portfolioid).then(res=>{
                console.log(res);
                this.capTableContentList=res.data.data;
                this.firstContentList=res.data.data.slice(0,1);
                this.otherContentList=res.data.data.slice(1);
            })
        },
        submitInputForm(fromName){
            this.$refs[capInputForm].validate((validate)=>{
                if(validate){
                    axioss.addShareHolder(obj).then(res=>{
                        console.log(res);
                        if(res.data.code=="SUCCESS"){
                            this.$message({
                                type:'success',
                                message: ''
                            })
                        }else{
                            this.$message({
                                type:'warning',
                                message: ''
                            })
                        }
                    })
                }else{
                    console.log('err submit')
                }
            })
        },
        scrolltoview(eletoview){
            var obj=this.$refs[eletoview];
            if(!this.heightObj){
                this.heightObj=this.$refs["CapTable"].offsetHeight
            }
            if(!obj){
                this.$refs["CapTable"].style.height=50+"px";
                this.$refs["CapTable"].style.overflow='hidden';
            }
            if(obj){
                var scrolly=window.scrollY
                var _top=obj.getBoundingClientRect().top;
                var top=_top+scrolly;
                document.documentElement.scrollTop=-(top-_top);
                obj.style.height=this.heightObj+"px"
            }
        },
        toHeader(data){
            var len=data.length;
            for(var i=0;i<len;i++){
                for(var j in data[i]){
                    if(/[0-9]/.test(j)){
                        data[i].header=(data[i])[j];
                    }
                }
            }
            return data;
        }
    },
    computed:{
        capTabelList(){
            return this.$store.state.capTabelList;
        },
        portfolioid(){
            if(this.$store.state.portfolioid==''){
                this.$store.dispatch('updateData');
            }
            return this.$store.state.portfolioid;
        }
    },
    directives:{
        isedit:{
            inserted:function(el){
                var inp=el.children[0];
                inp.onfocus=function(){
                    this.style.borderColor="#ccc";
                };
                inp.onblur=function(){
                    this.style.borderColor="transparent";
                }
            }
        }
    }
}
</script>
<style scoped lang="scss">

        .loan-table-container h3{
            font-size:16px;
            padding:5px 0;
            padding-left:10px;
            background:#eee;
            margin: 0;
            border-bottom: 1px solid #ebeef5;
        }

    .loan-table-container .table-foot{
        background:#eee;
        padding:5px 0;
        padding-left:10px;
    }
    .select-container{
        width:100%;
    }
    .capTable{
        margin-top:20px;
        border:1px solid #ddd;
    }
    .title{
        padding:10px;
        box-sizing: border-box;
        font-size: 20px;
        color:white;
        font-weight:bold;
        background:#00a1e9;
    }
    .loan-table-container .table-content{
        white-space: nowrap;
        overflow-x:scroll;
    }
</style>



